.sliderSection {
    width: 100%;
    display: flex;
    justify-content: center;

    .poularBrendsSlider {
        width: 100%;
        height: 200px;
        display: flex;
        justify-content: center;

        .slick-list {
            width: 100%;
            height: 100%;
            padding: 20px 0;

            .slick-track {
                height: 100%;


                .slick-slide {
                    height: 100%;
                    margin: 0 1px;
                    div{
                        .popularBrendsImages{
                            width: 250px;
                            height: 150px;
                            img{
                                width: 250px;
                                height: 150px;
                            }
                        }
                    }
                }
            }
        }


        .slick-prev {
            left: -10px;
            z-index: 10;
        }

        .slick-prev:before {
            color: black;
            font-size: 33px;
        }

        .slick-next {
            right: -10px;
            z-index: 10;
        }

        .slick-next:before {
            color: black;
            font-size: 33px;
        }

        .slick-dots {
            bottom: -50px;
        }
    }
}